import { useDrag, useDrop } from 'react-dnd';

const DraggableImage = ({ index, image, handleDelete, handleImageReorder, isRepresentative }) => {
  const [{ isDragging }, dragRef] = useDrag(() => ({
    type: 'image',
    item: { index },
    collect: (monitor) => ({
      isDragging: monitor.isDragging(),
    }),
  }));

  const [, dropRef] = useDrop(() => ({
    accept: 'image',
    hover: (item) => {
      const dragIndex = item.index;
      const hoverIndex = index;

      if (dragIndex === hoverIndex) {
        return;
      }

      handleImageReorder(dragIndex, hoverIndex);
      item.index = hoverIndex;
    },
  }));

  return (
    <div
      ref={node => {
        dragRef(node);
        dropRef(node);
      }}
      style={{ opacity: isDragging ? 0 : 1 }}
    >
      <div className="me-3 mb-3" style={{ position: 'relative' }}>
        <img src={image} alt={`Preview ${index}`} style={{ width: '150px', height: '150px', objectFit: 'cover' }} />
        {isRepresentative && <span style={{ position: 'absolute', top: 0, left: 0 }}></span>}
        <button onClick={() => handleDelete(index)} style={{ position: 'absolute', top: 0, right: 0 }}>Delete</button>
      </div>
    </div>
  );
};

export default DraggableImage;
